*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
  }

  .navbar {
    overflow: hidden;
    position: fixed;
    top: 0;
    width: 100%; 
  }
  
  .overview {
    float: left;
    margin-left: 0.5%;
  }

  .feedback {
    float: right;
    margin-right: 1%;
  }

  button {
    margin: 1%;
    width: 120px;
    height: 40px;
    border: none;
    outline: none;
    cursor: pointer;
    position: relative;
    border-radius: 10px;
  }
  
  h1 {
    text-align: center;
  }
  .wrapper {
    display: grid;  
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 1fr);
    width: 100vw;
    height: 90vh;
  }
  .one {
    border: 1px solid black;
    grid-column: 1;
    grid-row-start: 1;
    grid-row-end: 2;
    margin: 1%;
  }
  .two {
    border: 1px solid black;
    grid-column: 2;
    grid-row-start: 1;
    grid-row-end: 2;
    margin: 1%;
    }
  .three {
    border: 1px solid black;
    grid-column: 1;
    grid-row-start: 2;
    grid-row-end: 5;
    margin: 1%;

  }
  .four {
    margin: 1%;
    border: 1px solid black;
    grid-column: 2;
    grid-row-start: 2;
    grid-row-end: 5;
  }
.center{ 
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
}

.grid-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-radius: 5px;
  font-size: 1.2rem;
  text-align: center;
  display: flex;
  align-items: center;
}

.icon {
  width: 50px;
  height: 50px;
  margin-bottom: 5px;
}

.icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 8px;
}

.temperature {
  font-weight: bold;
  font-size: x-large; 
}

.news-item {
  margin: 3%;
  display: grid;
  grid-template-columns: 25% 75%;
  grid-template-rows: auto;
  grid-gap: 10px;
}

.image-container {
  grid-row: 1 / span 2;
}

.image-container img {
  width: 100%;
  height: auto;
}

.table-container {
  display: flex;
  align-items: center;
  padding-left: 10px;
}

table {
  border-collapse: collapse;
  width: 100%;
}

.stupid {
  border-bottom: none;
}


td {
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid black;
  padding: 5px 0;
}

a {
  text-decoration: none;
}

.active {
  color: green;
  background: lightgreen;
}

.passive {
  color: black;
  background: lightgrey;
}

@media (max-width: 768px) {
  .grid-container {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 480px) {
  .grid-container {
    grid-template-columns: 1fr;
  }
}